@import url(https://fonts.googleapis.com/css2?family=Freckle+Face&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Freckle+Face&display=swap);
body {
  margin: 0;
  background-color: #05698B;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}
nav{
    width: 100vw;
    height: 10vh;
    background-color: #00C2D1;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}

nav ul{
    margin: 0;
    width: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-evenly;
            justify-content: space-evenly;
}

nav ul li{
    font-family: 'Freckle Face', cursive;
    font-size: 3rem;
    color: #0A1045;
    list-style: none;
    transition: all linear 100ms;
    outline: none;
    -webkit-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

nav ul li:hover{
    cursor: pointer;
    color: #F9E900;
}

span.info{
    font-size: 3rem;
}

.mode-switch{
    font-size: 2.5rem;
    color: #0A1045;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}
.mode-switch:hover{
    cursor: auto;
    color: #0A1045
}

.mode-switch  input:hover{
    cursor: pointer;
}

.mode-switch input[type="checkbox"]{
    height: 4vh;
    width: 8vh;
    background: rgb(208, 208, 208);
    position: relative;
    -webkit-appearance: none;
    border-radius: 1vh 1vh;
    box-shadow: inset 0px 0px 5px rgba(0, 0, 0, 0.1);
    transition: all 200ms;
    margin-left: 1.5vh;
}

.mode-switch input[type="checkbox"]:before{
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    height: 4vh;
    width: 4vh;
    border-radius: 1vh 1vh;
    background: #FFF;
    box-shadow: 0px 0px 3px rgba(0, 0, 0, 0.2);
    transition: all 200ms;
}

.mode-switch input:checked[type="checkbox"]{
    background: #F9E900;
}

.mode-switch input:checked[type="checkbox"]:before{
    -webkit-transform: translate(4vh);
            transform: translate(4vh);
}

.grid-section{
    width: 100vw;
    height: 90vh;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}

.path-finder-grid{
    background-color: #FFF;
    padding: 0;
    display: grid;
    width: 90%;
    min-height: 90%;
    grid-template-columns: repeat(25, 1fr);
    border-radius: 5px;
    overflow: hidden;
}
.tutorial-section{
    position: fixed;
    height: 100vh;
    width: 100vw;
    display: grid;
    place-content: center;
    background-color: rgba(0, 0, 0, 0.8);
    z-index: 1;
}

.tutorial-wrapper{
    height: 75vh;
    width: 75vw;
    background-color: #00c2d1;
    position: relative;
    border-radius: 2vh;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    z-index: 1;
    overflow: hidden;
}

.tutorial-section .tutorial-header{
    font-family: 'Freckle Face', cursive;
    font-size: 4rem;
    padding: 0;
    margin: 0;
    text-shadow: 1px 1px 4px rgba(0, 0, 0, 0.5);
    color: #f7ea30;
    z-index: 1;
}

.tutorial-wrapper .close-icon{
    color: white;
    font-size: 6rem;
    position: absolute;
    top: 0;
    right: 0;
    cursor: pointer;
    transition: all 300ms;
    z-index: 3;
    -webkit-animation: shake 10s infinite linear;
            animation: shake 10s infinite linear;
}

.tutorial-wrapper .close-icon:hover{
    color: #f7ea30;
}

div.carousel-item{
    height: 100%;
    width: 100%;
}

div.carousel-item img{
    height: 100%;
    width: 100%;
}

@-webkit-keyframes shake {
    40%{-webkit-transform: rotate(0deg);transform: rotate(0deg);}
    41%{-webkit-transform: rotate(20deg);transform: rotate(20deg);}
    42%{-webkit-transform: rotate(-20deg);transform: rotate(-20deg);}
    43%{-webkit-transform: rotate(0deg);transform: rotate(0deg);}
    90%{-webkit-transform: rotate(0deg);transform: rotate(0deg);}
    91%{-webkit-transform: rotate(-20deg);transform: rotate(-20deg);}
    92%{-webkit-transform: rotate(20deg);transform: rotate(20deg);}
    93%{-webkit-transform: rotate(0deg);transform: rotate(0deg);}
}

@keyframes shake {
    40%{-webkit-transform: rotate(0deg);transform: rotate(0deg);}
    41%{-webkit-transform: rotate(20deg);transform: rotate(20deg);}
    42%{-webkit-transform: rotate(-20deg);transform: rotate(-20deg);}
    43%{-webkit-transform: rotate(0deg);transform: rotate(0deg);}
    90%{-webkit-transform: rotate(0deg);transform: rotate(0deg);}
    91%{-webkit-transform: rotate(-20deg);transform: rotate(-20deg);}
    92%{-webkit-transform: rotate(20deg);transform: rotate(20deg);}
    93%{-webkit-transform: rotate(0deg);transform: rotate(0deg);}
}
.node{
    height: 100%;
    width: 100%;
    background-size: 100% 100%;
	/* animation: flip-wall-to-path 300ms linear; */
}

.start-node{
    background-color: #44e6f2;
    background-image: none;
}

.end-node{
    background-color: #e32c2c;
    background-image: none;
}

.wall-node{
    background-color: #7DD669;
	/* animation: flip-path-to-wall 500ms linear; */
}

.start-node.visited-node{
    background-color: #44e6f2;
    background-size: 100% 100%;
}

.end-node.visited-node{
    background-color: #e32c2c;
    background-size: 100% 100%;
}

.end-node.visited-node.path-node{
    background-color: #ff1e1e;
    background-size: 100% 100%;
}

.visited-node{
    background-color: #dedede;
}

/* rgb(77, 159, 131) */

.path-node{
    background-size: 100% 100%;
    background-color: #f7ea30;
    -webkit-filter: brightness(150%);
            filter: brightness(150%)
}
